// styled components
import { StyledAuthorItems, CollectionsGrid } from "./style";

// components
import StyledTabs from "@ui/StyledTabs";
import ItemsGrid from "@components/ItemsGrid";
import CollectionItem from "@components/CollectionItem";
import AuthorTag from "@ui/AuthorTag";
import LoadMore from "./LoadMore";

// hooks
import { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import axios from "axios";

const Collections = ({ content }) => {
  return <ItemsGrid items={content} isPrivate />;
};

const AuthorItems = () => {
  const currentUrl = useLocation();
  const [collections, setCollections] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [continuation, setContinuation] = useState(null);
 

  const loadMoreContinuation = async () => {
    setIsLoading(true);
  
    try {
      const { data } = await axios.get(
        `https://api-goerli.reservoir.tools/tokens/v6?collection=${
          currentUrl.pathname.split("/")[2]
        }&continuation=${continuation}`
      );
  
      const newCollections = data.tokens.map((item) => ({
        id: item.token.tokenId,
        image: item.token.image,
        price: (item.market.floorAsk.price == null ? "-" : item.market.floorAsk.price.amount.decimal),
        owner: item.token.owner,
        maker: item.market.floorAsk.source.domain,
        sourceicon: item.market.floorAsk.source.icon,
      }));
  
      setCollections((prevCollections) => [...prevCollections, ...newCollections]);
      setContinuation(data.continuation);
      setIsLoading(false);
    } catch (error) {
      console.error(error);
      setIsLoading(false);
    }
  };
  

  const loadCollections = async () => {
    setIsLoading(true);


    try {
      const { data } = await axios.get(
        `https://api-goerli.reservoir.tools/tokens/v6?collection=${
          currentUrl.pathname.split("/")[2]
        }`
      );

     

      const newCollections = data.tokens.map((item) => ({
        id: item.token.tokenId,
        image: item.token.image,
        price: (item.market.floorAsk.price == null ? "-" : item.market.floorAsk.price.amount.decimal),
        owner: item.token.owner,
        maker: item.market.floorAsk.source.domain,
        sourceicon: item.market.floorAsk.source.icon,
      }));

      setCollections((prevCollections) => [...prevCollections, ...newCollections]);
      setContinuation(data.continuation);
      setIsLoading(false);
    } catch (error) {
      console.error(error);
      setIsLoading(false);
    }
  };

  useEffect(() => {
    loadCollections();
  }, []);

  const tabs = [
    {
      label: `Collections`,
      key: "item-2",
      children: <Collections content={collections} />,
    },
  ];

  return (
    <StyledAuthorItems>
      <div className="container">
        <StyledTabs tabs={tabs} />
        <LoadMore
          isLoading={isLoading}
          loadMore={loadMoreContinuation}
          showLoadMore={continuation !== null}
        />
      </div>
    </StyledAuthorItems>
  );
};

export default AuthorItems;