// styled components
import StyledAuthorCard from './style';

// components
import Socials from '@components/Socials';
import Avatar from '@ui/Avatar';
import CollapsedText from '@components/CollapsedText';
import Spring from '@components/Spring';

// hooks
import {useEffect, useState} from 'react';
import {useWindowSize} from 'react-use';
import useMeasure from 'react-use-measure';

// utils
import {toast} from 'react-toastify';
import {truncateMiddle} from '@utils/helpers';
import classNames from 'classnames';
import axios from 'axios';
import { SweepModal } from '@reservoir0x/reservoir-kit-ui'


// assets
import avatar from '@assets/avatar.webp';
import { useLocation } from 'react-router-dom';
import eth from '@assets/icons/eth.svg';


const AuthorCard = () => {
    const [ref, {width}] = useMeasure();
    const [onsale, setonSale] = useState([]);
    const currentUrl = useLocation();
    const [profilepic, setprofilepic] = useState([])
    const [collectionName , setCollectioName] = useState([])
    const [description, setdescription] = useState([])
    const [banner, setbanner] = useState([])
    const [totalSupply, setotalSupply] = useState([])
    const [floorPrice, setFloorPrice] = useState(['-'])
    const [volume, setVolume] = useState(['-'])
    const [empty , setEmpty] = useState(['-'])
    const [collData, setColldata] = useState([])

    const [isCopied, setIsCopied] = useState(false);

    const handleCopyUrl = () => {
      navigator.clipboard.writeText(window.location.href);
      setIsCopied(true);
      setTimeout(() => setIsCopied(false), 3000);
      toast.info('Clipboard copied ');
    };


    useEffect(() => {
        (async () => {
          const { data } = await axios.get(
            `https://api-goerli.reservoir.tools/collections/v5?id=${currentUrl.pathname.split("/")[2] }&includeTopBid=true&includeOwnerCount=true`
          );
          setColldata(data.collections[0])
          setbanner(data.collections[0].banner)
          setCollectioName(data.collections[0].slug)
          setprofilepic(data.collections[0].image)
          setdescription(data.collections[0].description)
          setonSale(data.collections[0].onSaleCount)
          setotalSupply(data.collections[0].tokenCount)
          setFloorPrice(data.collections[0].floorAsk.price.amount.native)
          setVolume(data.collections[0].volume.allTime)
          if(data.collections[0].topBid.price != 'null') {
            setEmpty(data.collections[0].topBid.price.amount.native);
          }
          
        })();
      }, []);

    const id = `${currentUrl.pathname.split("/")[2] }`
    const bio = `${description}`

    const handleCopy = () => {
        navigator.clipboard.writeText(id);
        toast.success('Copied to clipboard');
    }

    const formattedNumber = volume!= 0 && volume == null ? (volume / 10000).toLocaleString("en-US", {
        minimumFractionDigits: 0,
        maximumFractionDigits: 1,
      }) + "K": '-' ;


      const formattedNumberComma = totalSupply.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      

    return (
        <StyledAuthorCard>
            <div  style={{height:"200px"}}>
               <img src={banner}/>
            </div>
            <div className="container">
                   <Spring className="main d-flex flex-column g-30 bg-primary border-10">
                       <div className="main_header d-flex">
                           <Avatar src={profilepic} isVerified={true}/>
                           {/* <img src={profilepic} /> */}
                           <div className="main_header-follow d-flex align-items-center g-20">
                            <span className="d-flex flex-column">
                                <span className="h6">{formattedNumberComma}</span>
                                <span className="meta">Token Supply</span>
                            </span>
                               <span className="d-flex flex-column">
                                <span className="h6">{onsale}</span>
                                <span className="meta">On Sale </span>
                            </span>
                            {/* <img src= {eth} style={{ width:"10px"}}/> */}
                            <span className="d-flex flex-column">
                            
                                <span className="h6">{floorPrice}</span>
                                
                                <span className="meta">Floor Price </span>
                            </span>

                            <span className="d-flex flex-column">
                            
                            <span className="h6">{formattedNumber}</span>
                            <span className="meta"> Total Volume </span>
                        </span>

                        <span className="d-flex flex-column">
                            
                                <span className="h6">{empty}</span>
                                <span className="meta">Top Offer</span>
                            </span>
                            
                           </div>
                           <div className="main_header-actions d-flex g-10">

                           {/* <SweepModal
                                 trigger={
                                    <button className="btn btn--icon" aria-label="Share profile" onClick={handleCopyUrl}>
                                    <i className="icon icon-share"/>
                                </button>
                                

                                 }
                                                        collectionId={id}
                        onSweepComplete={(data) => {
                            console.log('Sweep Complete', data)
                        }}
                        onSweepError={(error, data) => {
                            console.log('Sweep Error', error, data)
                        }}
                        onClose={() => {
                            console.log('SweepModal Closed')
  }}
                            /> */}

                                <button className="btn btn--icon" aria-label="Share profile" onClick={handleCopyUrl}>
                                    <i className="icon icon-share"/>
                                </button>
                             
                               {/* <button className="btn btn--icon" aria-label="Menu">
                                   <i className="icon icon-ellipsis"/>
                               </button> */}
                           </div>
                       </div>
                       <div className="main_info">
                           <h4 className="main_info-name">{collectionName}</h4>
                           <div className="main_info-id d-flex flex-wrap align-items-center text-sm">
                               {/* <span className="text-bold text-light">@ventuniconeymon</span> */}
                               <div className="d-flex align-items-center g-10">
                                   <span>{truncateMiddle(id, 5, 4)}</span>
                                   <button className="text-accent" onClick={handleCopy} aria-label="Copy ID">
                                       <i className="icon-copy"/>
                                   </button>
                               </div>
                           </div>
                           <Socials value ={collData} className="main_info-socials"/>
                           <div className="main_info-bio" ref={ref}>
                               <CollapsedText width={width} text={bio} lines={3} withButton />
                           </div>
                       </div>
                   </Spring>
            </div>
        </StyledAuthorCard>
    )
}

export default AuthorCard

